@use "../../foundation/mixins" as *;
@use "../../foundation/variables" as *;
// ご利用の流れ
.p-use {
  padding: 5.4rem 0 9.1rem 0;
  @include sp {
    padding: 4.6rem 0 0 0;
  }
  &__flow {
    position: relative;
    // row-gap適用するためにdisplay: gridを使用
    display: grid;
    row-gap: 5.8rem;
    margin: 5.3rem 0 0;
    @include sp {
      // display: initial;
      // margin-top: 3.9rem;
      margin: 3.9rem 0 0;
      row-gap: 3.6rem;
    }
    &-detail {
      display: flex;
      align-items: center;
      justify-content: center;

      &-inner {
        display: flex;
        // width: 29rem;
        @include sp {
          display: initial;
        }
      }
      &__en {
        display: flex;
        align-items: center;
        width: 15.4rem;
        font-size: 2.2rem;
        line-height: calc(33 / 22);
        margin-right: 3.8rem;
      }
      &__circle {
        display: flex;
        align-items: center;
        flex-direction: column;
        background-color: rgb(203, 0, 0);
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
        margin-right: 4.3rem;
        @include sp {
          margin-right: initial;
        }
      }
      &__ja {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 56.7rem;
        font-size: 1.8rem;
        line-height: calc(30 / 18);
        @include sp {
          width: 28.7rem;
          margin-top: 0.9rem;
          font-size: 1.6rem;
          line-height: calc(30 / 16);
        }
      }
    }

    // 矢印の屋根部分
    // :last-of-type &::before {
    :nth-child(1) &::before {
      left: 2.5%;
      top: 100%;
      content: "";
      position: absolute;
      transform: rotate(-45deg);
      width: 2.5rem;
      height: 2.5rem;
      border-bottom: 0.3rem solid rgb(203, 0, 0);
      border-left: 0.3rem solid rgb(203, 0, 0);
      transform-origin: left top;
      @include sp {
        left: -1.2%;
      }
    }
    // 縦棒部分
    :nth-child(1) &::after {
      content: "";
      position: absolute;
      transform: translateX(-50%);
      width: 0.3rem;
      height: 40.9rem;
      background-color: rgb(203, 0, 0);
      transform-origin: left top;
      left: 4.5%;
      top: 5%;
      @include sp {
        top: 10%;
        height: 67rem;
      }
    }
  }
}
