@use "../../foundation/mixins" as *;
@use "../../foundation/variables" as *;
.p-header {
  z-index: 1000;
  width: 100%;
  height: 8rem;
  left: 0;
  top: 0;
  transition: all 0.3s;
  position: fixed;
  background-color: $color-white;
  @include sp {
    height: 5rem;
    padding-right: 0;
    @include sp {
      // width: initial;
    }
  }
  & .inner {
    @include sp {
      width: 100%;
    }
  }
  &__wrapper {
    display: flex;
    @include sp {
      padding-left: 2rem;
      height: 5rem;
      justify-content: space-between;
    }
  }
  &__logo {
    display: flex;
    align-items: center;
    height: 8rem;
    @include sp {
      display: initial;
      height: 5rem;
    }
  }
  &__items {
    display: flex;
    align-items: center;
    @include sp {
      // height: 5rem;
    }
  }
  &__item {
    margin-right: 0.7rem;
    @include sp {
      display: flex;
      align-items: center;
    }
    // ヘッダーのロゴとリンク部分
    a {
      @include sp {
        display: flex;
        align-items: center;
        height: 5rem;
        img {
          @include sp {
            height: 3.1rem;
            width: 2rem;
          }
        }
      }
    }
  }

  // ハンバーガー三本線部分
  &__hb {
    &.active {
      span {
        &:nth-child(1) {
          transform: rotate(45deg) translate(0.8rem, 0.8rem);
        }

        &:nth-child(2) {
          background-color: transparent;
        }

        &:nth-child(3) {
          transform: rotate(-45deg) translate(0.8rem, -0.8rem);
        }
      }
    }
  }

  // ハンバーガーボタン部分（PCで非表示でSPで表示）
  &__scroll-bg {
    display: none;
    @include sp {
      display: flex;
      justify-content: space-between;
    }
    // ハンバーガーopen時の動作（三→×）
  }
  &__nav-wrapper {
    display: flex;
    align-items: center;
    column-gap: 3.3rem;
    margin-left: auto;
    @include sp {
      display: none;
    }
  }
  // ナビゲーション部分のお問い合わせボタン
  &__button {
    height: 4.5rem;
    display: flex;
    padding: 0.6rem 3rem;
    a {
      line-height: calc(33 / 15);
      font-size: 1.5rem;
    }
  }

  &__nav {
    ul {
      display: flex;
      column-gap: 3.3rem;
      li {
        height: 8rem;
        // width: 100%;
      }
      a {
        display: flex;
        align-items: center;
        height: 8rem;
        width: 100%;
        transition: all 0.3s;
        cursor: pointer;
      }
    }
  }

  @include sp {
    &__hb-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 7.2rem;
      height: 5rem;
      background-color: $color-red;
      row-gap: 0.7rem;
      cursor: pointer;
    }
    &__hb-line {
      @include sp {
        transition: 0.3s;
        display: block;
        background-color: $color-white;
        border-radius: 0.2rem;
        width: 3.2rem;
        height: 0;
        padding-block: 0.2rem;
      }
    }
  }
}
